.skills-title {
  color: var(--main-text-color);
  margin-top: 0em;
  font-weight: bolder;
  text-align: center;
  width: 50%;
  margin: 0 auto;
}

.skills-title:hover {
  cursor: default;
}

.skill-header {
  color: var(--accent-color-1);
  padding-bottom: 1rem;
}

.skill-item {
  color: var(--main-text-color);
}





@media (max-width: 480px) {
  #skills-wrapper {
    background: none;
  }
  .skills-title {
    font-size: 1em;
  }
  .skill-header {
    font-size: 1.2em;
  }

  .skill-item {
    font-size: 0.7em;
  }
  .section-header{
    font-size:2.25em;
    padding-bottom:0;
  }
}

@media (min-width: 481px) {
  #skills-wrapper {

    transition-duration: 2s;
    transition-timing-function: cubic-bezier(0.16, 1, 0.3, 1);

    z-index: -100;
    background-attachment: fixed;
    background-image: url("./icons.svg");
    background-size:30%;
    background-repeat: repeat;
  }
  .skill-item {
    font-size: 1.1em;
    padding: 0.3em;
    transition: transform 0.2s ease-out;
  }

  .skill-item:hover {
    transform: scale(1.3, 1.3);
    cursor: default;
  }

  .skills-window {
    backdrop-filter: blur(25px)!important;
    background-color: var(--main-bg-color);
    -webkit-backdrop-filter: blur(25px);
    border-radius: 0.5em;
    padding-bottom: 2rem;
    margin-left:auto;
    margin-right:auto;
    margin-top: 2em;
    margin-bottom: 1em;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.12), 0 2px 4px rgba(0, 0, 0, 0.24);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  }

}

.skills-header:hover {
  cursor: default;
}
.skill-list {
  margin-right: 0;
  padding: 0;
}

.fill-width {
  width: 100%;
  cursor: default;
}

.skills-content-wrapper {
  height: 100%;
  padding-bottom: 15vh;
}

.skills-content {
  color: var(--light);
  padding-top: 3em;
  opacity: 1;
}

#skills {
  background-color: transparent;
  margin-top: 10em;
  margin-bottom: 10em;
  border-radius: 0.5em;
}

.projects-btn {
  margin-top: 2em;
  border-radius: 0.4em;
  border: 1px solid var(--accent-color-1);
  margin-left: 35%;
  width: 30%;
  text-align: center;
  backdrop-filter: blur(5px);
  transition: transform 1s ease-in-out;
}

.projects-btn:hover{
  transform: scale(1.5);
}
